export const CountryFlagSVG: React.FC<CountryFlagSVGProps> = ({ countryCode, className = '', size = 'md' }) => {
    const code = countryCode?.toUpperCase() || '';
    const svgContent = flagSvgs[code];
    const emoji = flagEmojis[code] || '🌐';
    const sizeClass = size === 'sm' ? 'h-3 w-4' : size === 'lg' ? 'h-6 w-8' : 'h-4 w-5';

    if (svgContent) {
        return (
            <svg 
                className={`${sizeClass} ${className} inline-block`}
                dangerouslySetInnerHTML={{ __html: svgContent }}
            />
        );
    }

    return (
        <span className={`${className} leading-none`}>
            {emoji}
        </span>
    );
};

export const getCountryName = (countryCode: string): string => {
    const code = countryCode?.toUpperCase() || '';
    return countryNames[code] || code || 'Unknown';
};

interface CountryBadgeProps {
    countryCode: string;
    showName?: boolean;
    className?: string;
    size?: 'sm' | 'md' | 'lg';
}

export const CountryBadge: React.FC<CountryBadgeProps> = ({ 
    countryCode, 
    showName = true, 
    className = '', 
    size = 'md' 
}) => {
    const name = getCountryName(countryCode);
    const sizeClass = size === 'sm' ? 'text-sm' : size === 'lg' ? 'text-lg' : 'text-base';
    
    return (
        <span className={`flex items-center gap-1 ${sizeClass} ${className}`}>
            <CountryFlagSVG countryCode={countryCode} size={size} />
            {showName && <span>{name}</span>}
        </span>
    );
};
